*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  color: inherit;
  font: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  border: none;
  background-color: transparent;
}

img, picture, svg, video {
  display: block;
  height: auto;
  vertical-align: middle;
  font-style: italic;
  background-repeat: no-repeat;
  background-size: cover;
}

.content-grid {
  --breakout-size: calc((var(--template-breakout-max-width) - var(--template-max-width)) / 2);
  display: grid;
  grid-template-columns: [full-width-start] minmax(var(--template-padding), 1fr) [breakout-start] minmax(0, var(--breakout-size)) [content-start] min(100% - var(--template-padding) * 2, var(--template-max-width)) [content-end] minmax(0, var(--breakout-size)) [breakout-end] minmax(var(--template-padding), 1fr) [full-width-end];
}

.content-grid > :not(.breakout, .full-width),
.full-width > :not(.breakout, .full-width) {
  grid-column: content;
}

.content-grid > .breakout {
  grid-column: breakout;
}

.content-grid > .full-width {
  grid-column: full-width;
  display: grid;
  grid-template-columns: inherit;
}

:root {
  --template-padding: 24px;
  --template-max-width: 1400px;
  --template-breakout-max-width: var(--template-max-width);
  --bg-blue: #D1EBEE;
  --bg-red: #F7E4E0;
  --bg-yellow: #F5F1E3;
  --border-radius: 24px;
  --text-link: rgb(27, 154, 170);
  --text-link-hover: rgb(73, 174, 187);
  --title-gap: 56px;
  --gap: 40px;
  --section-gap: 112px;
}

.text-header-1 {
  line-height: 1;
  font-size: 3.8125rem;
  font-weight: 700;
}

.text-header-2 {
  line-height: 1;
  font-size: 3.0625rem;
  font-weight: 700;
}

.text-header-3 {
  line-height: 1;
  font-size: 2.4375rem;
  font-weight: 700;
}

.text-header-4 {
  line-height: 1;
  font-size: 1.9375rem;
  font-weight: 700;
}

.text-header-5 {
  line-height: 1;
  font-size: 1.5625rem;
  font-weight: 600;
}

.text-header-6 {
  line-height: 1;
  font-size: 1.25rem;
  font-weight: 600;
}

.text-subtitle-1 {
  font-weight: 400;
  font-size: 1.5625rem;
  line-height: 1.25;
}

.text-body-1 {
  /* body-1 */
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.25;
}

.text-body-2 {
  /* body-2 */
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25;
}

.text-center {
  text-align: center;
}

.bg-blue {
  background-color: var(--bg-blue);
}

.bg-red {
  background-color: var(--bg-red);
}

.bg-yellow {
  background-color: var(--bg-yellow);
}

.mb-8 {
  margin-bottom: 8px;
}

.mb-16 {
  margin-bottom: 16px;
}

.mb-32 {
  margin-bottom: 32px;
}

.mt-8 {
  margin-top: 8px;
}

.mt-16 {
  margin-top: 16px;
}

.mt-32 {
  margin-top: 32px;
}

a {
  color: var(--text-link);
}
a:hover {
  color: var(--text-link-hover);
}

body {
  font-family: "Golos Text";
  font-optical-sizing: auto;
  /* body-1 */
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.25;
}
body.no-scroll {
  overflow: hidden;
}

h1 {
  font-weight: 700;
  font-size: 4.75rem;
  line-height: 1;
}

ul {
  padding-left: 24px;
}

.warning {
  position: relative;
  /* body-2 */
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25;
  font-weight: 500;
  padding: 4px 12px 4px 34px;
  background: var(--bg-red);
  color: rgb(213, 122, 102);
  border-radius: 16px;
}
.warning::before {
  position: absolute;
  left: 4px;
  content: "!";
  display: block;
  color: white;
  border-radius: 50%;
  background: rgb(213, 122, 102);
  aspect-ratio: 1/1;
  width: 20px;
  height: 20px;
  text-align: center;
}

.social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #fff;
}
.social-link--vk .icon-svg {
  width: 20px;
  height: 13px;
}
.social-link--tg .icon-svg {
  width: 20px;
  height: 18px;
}
.social-link--whatsup .icon-svg {
  width: 20px;
  height: 20px;
}
.social-link--ig .icon-svg {
  width: 20px;
  height: 20px;
}

.select-group {
  border: 2px solid rgb(209, 235, 238);
  border-radius: 16px;
  background-color: #fff;
  padding: 8px;
  display: flex;
  flex-direction: row;
}
.select-group__item {
  background-color: inherit;
  border-radius: 12px;
  padding: 8px;
  text-align: center;
  font-size: 1.125rem;
  font-weight: 500;
  border: none;
  flex-grow: 1;
}
.select-group__item--active {
  color: #fff;
  background: rgb(213, 122, 102);
}

.label {
  /* body-1 */
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.25;
  border-radius: 100px;
  padding: 3px 6px;
  color: #fff;
  background: rgb(213, 122, 102);
}

.tag {
  padding: 8px 16px;
  font-size: 1.125rem;
  font-weight: 500;
  border: 1.5px solid rgba(27, 154, 170, 0.2);
  border-radius: 100px;
  color: #000;
  background-color: #fff;
  cursor: pointer;
}
.tag--active {
  background: rgb(27, 154, 170);
  color: #fff;
}
.tag--disabled, .tag:disabled {
  cursor: default;
  color: rgba(27, 154, 170, 0.2);
}

.button-arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1.5px solid rgb(224, 224, 224);
  color: black;
  background-color: transparent;
}
.button-arrow .icon-svg {
  width: 24px;
  height: 24px;
}
.button-arrow--link {
  margin-left: 12px;
  display: inline-flex;
  width: 26.67px;
  height: 26.67px;
  border-color: currentColor;
  color: currentColor;
}
.button-arrow--link .icon-svg {
  width: 12px;
  height: 12px;
}
.button-arrow--left .icon-svg {
  transform: rotate(180deg);
}
.button-arrow--down .icon-svg {
  transform: rotate(90deg);
}
.button-arrow--up .icon-svg {
  transform: rotate(-90deg);
}
.button-arrow:hover {
  color: var(--text-link-hover);
  border-color: var(--text-link-hover);
}
.button-arrow--filled {
  color: #fff;
  background-color: var(--text-link);
  border-color: var(--text-link);
}
.button-arrow--filled:hover {
  color: #fff;
  background-color: var(--text-link-hover);
  border-color: var(--text-link-hover);
}

.button-audio {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--text-link);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 1.5px solid var(--text-link);
  background-color: #fff;
}
.button-audio:hover {
  color: var(--text-link-hover);
  border-color: var(--text-link-hover);
}
.button-audio .icon-svg {
  width: 12px;
  height: 12px;
}
.button-audio--pause .icon-svg {
  width: 16px;
  height: 16px;
}

.button {
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px 16px;
  gap: 10px;
  background: linear-gradient(180deg, #49AEBB 0%, #1B9AAA 100%);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  color: white;
  font-size: 1rem;
  line-height: 1rem;
  letter-spacing: 0.2px;
  font-weight: 600;
}
.button:hover {
  background: rgb(73, 174, 187);
}
.button:disabled {
  opacity: 0.4;
}
.button--block {
  width: 100%;
}
.button--nowrap {
  white-space: nowrap;
}
.button--lg {
  padding: 24px 32px;
  font-size: 1.5625rem;
  line-height: 2rem;
  letter-spacing: 0.5px;
}
.button--md {
  padding: 16px 28px;
  font-size: 1.25rem;
  line-height: 1.5rem;
}
.button--outlined {
  box-shadow: none;
  background: white;
  color: rgb(27, 154, 170);
  border: 2px solid rgb(27, 154, 170);
}
.button--outlined:hover {
  background: white;
  color: rgb(73, 174, 187);
  border: 2px solid rgb(73, 174, 187);
}

.input {
  border-radius: 16px;
  background: rgb(255, 255, 255);
  color: black;
  font-size: 1.125rem;
  line-height: 1.5rem;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 15px 16px;
  border: 1px solid white;
}
.input::placeholder {
  color: rgb(130, 130, 130);
}
.input--error {
  border-color: rgb(213, 122, 102);
  color: rgb(213, 122, 102);
}
.input--block {
  display: block;
  width: 100%;
}

.audio-controls__play, .audio-controls__pause {
  display: grid;
  gap: 8px;
  grid-auto-flow: column;
  justify-content: start;
  align-items: center;
}
.audio-controls__progress {
  /* body-2 */
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25;
  color: rgb(130, 130, 130);
}
.audio-controls__progress .icon-svg {
  width: 20px;
  height: 17px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 64px;
  z-index: 2;
  padding: 8px 0;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(16px);
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__nav {
  font-size: 1rem;
  font-weight: 500;
  display: grid;
  gap: 32px;
  grid-auto-flow: column;
}
.header__nav a {
  color: #000;
}
.header__nav a:hover {
  opacity: 0.6;
}
.header__user {
  display: flex;
  align-items: center;
}
.header__user__profile {
  width: 32px;
  height: 32px;
  margin-right: 16px;
  color: #1B9AAA;
}
.header__user__profile:hover {
  color: rgb(73, 174, 187);
}
.header__burger {
  display: none;
  margin-left: 24px;
  width: 24px;
  height: 24px;
  color: #1B9AAA;
}

.section {
  margin-top: var(--section-gap);
}
.section__wrapper {
  z-index: 1;
}
.section__title {
  line-height: 1;
  font-size: 3.0625rem;
  font-weight: 700;
  margin-bottom: var(--title-gap);
}
.section--main {
  margin-top: 0;
}
.section--main-inner {
  margin-top: 0;
}
.section--prices {
  margin-top: var(--section-gap);
  padding-top: var(--section-gap);
  padding-bottom: 69px;
}
.section--start {
  margin-top: var(--section-gap);
  padding-top: var(--section-gap);
  padding-bottom: var(--section-gap);
}
.section:last-child {
  margin-bottom: var(--section-gap);
}

.breadcrumbs {
  font-size: 0.875rem;
}
.breadcrumbs__link-fake {
  opacity: 0.5;
}
.breadcrumbs__link {
  position: relative;
  color: #000;
  opacity: 0.8;
  margin-right: 24px;
}
.breadcrumbs__link::after {
  position: absolute;
  content: "";
  display: block;
  right: -24px;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  background-image: url("data:image/svg+xml;base64,");
  opacity: 0.3;
}

.main-inner {
  padding-top: 88px;
  padding-bottom: 78px;
  min-height: 100vh;
  min-height: 100lvh;
}
.main-inner .section__wrapper {
  margin-top: var(--section-gap);
  display: flex;
  gap: var(--gap);
}
.main-inner__left {
  flex: 1 0 0;
  padding-top: 31px;
}
.main-inner__right {
  flex: 1 0 0;
}
.main-inner__title {
  line-height: 1;
  font-size: 3.8125rem;
  font-weight: 700;
  margin-bottom: 32px;
}
.main-inner__description {
  font-weight: 400;
  font-size: 1.5625rem;
  line-height: 1.25;
  margin-bottom: 40px;
}
.main-inner .button {
  position: relative;
}
.main-inner .button::after {
  display: block;
  content: "";
  position: absolute;
  left: -60px;
  bottom: 0;
  transform: translate(-100%, 0) scaleX(-1);
  width: 136px;
  height: 121px;
  background-image: url(../images/decor-arrow-1.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.main-inner__image-container {
  position: relative;
  border-radius: 48px;
}
.main-inner__image-container::before {
  position: absolute;
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: inherit;
  transform: rotate(-10deg);
  opacity: 0.8;
}
.main-inner__image {
  position: relative;
  z-index: 1;
  border-radius: 48px;
  width: 100%;
}

.main {
  position: relative;
  color: #fff;
  font-size: 1.5625rem;
  line-height: 1.25;
  padding-top: 160px;
  padding-bottom: 78px;
}
.main .section__wrapper {
  max-width: 680px;
}
.main__bg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.main__bg {
  grid-column: full-width !important;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0;
  transition: 1s opacity;
  background-image: var(--bg);
}
@media (max-width: 1279px) {
  .main__bg {
    background-image: var(--bg-tablet, var(--bg));
  }
}
@media (max-width: 767px) {
  .main__bg {
    background-image: var(--bg-phone, var(--bg));
  }
}
.main__bg.is-active {
  opacity: 1;
}
.main__title {
  margin-bottom: 32px;
}
.main__signup-form {
  margin-top: 64px;
  position: relative;
}
.main__signup-form::after {
  display: block;
  content: "";
  position: absolute;
  right: -80px;
  top: 50%;
  transform: translate(100%, -50%);
  width: 136px;
  height: 121px;
  background-image: url(../images/decor-arrow-1.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.form .warning {
  margin-top: 8px;
}
.form__row {
  margin-bottom: 8px;
  display: flex;
  align-items: center;
}
.form__row--last {
  margin-top: 16px !important;
  margin-bottom: 0 !important;
}
.form__row-inline {
  margin-bottom: 8px;
  display: grid;
  gap: 8px;
  grid-auto-flow: column;
  align-items: center;
}

.signup-form {
  border-radius: var(--border-radius);
  color: black;
  padding: 40px;
}
.signup-form--popup {
  padding: 0 !important;
}
.signup-form p {
  font-weight: 400;
  font-size: 1.5625rem;
  line-height: 1.25;
}
.signup-form__title {
  line-height: 1;
  font-size: 1.9375rem;
  font-weight: 700;
  margin-bottom: 32px;
  max-width: 400px;
  display: flex;
}
.signup-form__warn {
  margin-left: 12px;
  font-size: 0.8125rem;
  line-height: 1.25;
  color: rgb(130, 130, 130);
}

.speak__title {
  text-align: center;
  line-height: 1;
  font-size: 3.0625rem;
  font-weight: 700;
  margin-bottom: 24px;
}
.speak__subtitle {
  font-weight: 400;
  font-size: 1.5625rem;
  line-height: 1.25;
  margin-bottom: var(--title-gap);
  text-align: center;
  color: #828282;
}
.speak__cards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--gap);
}
.speak__card {
  border-radius: var(--border-radius);
  padding: 64px 40px 40px;
  background-size: contain;
  background-position: right bottom;
  background-repeat: no-repeat;
  height: 428px;
}
.speak__card:nth-child(1) {
  background-image: url(../images/card-bg-1.jpeg);
}
.speak__card:nth-child(2) {
  background-image: url(../images/card-bg-2.jpeg);
}
.speak__card:nth-child(3) {
  grid-column: 1/3;
  display: flex;
  gap: 50px;
}
.speak__card__title {
  line-height: 1;
  font-size: 1.9375rem;
  font-weight: 700;
  margin-bottom: 24px;
  max-width: 50%;
}
.speak__card__description {
  /* body-1 */
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.25;
  opacity: 0.6;
  max-width: 50%;
}
.speak__card__left {
  flex: 0 0 25%;
}
.speak__card__left > div {
  max-width: 100%;
}
.speak__card__slider {
  display: flex;
  gap: 20px;
  overflow: hidden;
}
.speak__card__slider img {
  width: 533px;
  height: 300px;
}

.test .section__wrapper {
  display: flex;
  flex-direction: row;
  gap: var(--gap);
  align-items: center;
}
.test__image {
  width: 560px;
}
.test__right {
  display: flex;
  flex-direction: column;
  gap: 32px;
  align-items: flex-start;
}
.test__title {
  line-height: 1;
  font-size: 3.8125rem;
  font-weight: 700;
}
.test__description {
  /* body-1 */
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.25;
}

.courses__items {
  display: grid;
  grid-auto-flow: row;
  gap: 40px;
  justify-items: center;
}

.courses-item {
  display: grid;
  gap: var(--gap);
  align-items: center;
  grid-auto-flow: column;
  grid-template-columns: minmax(auto, 560px) minmax(auto, 560px);
}
.courses-item__title {
  line-height: 1;
  font-size: 2.4375rem;
  font-weight: 700;
  margin-bottom: 32px;
}
.courses-item__description {
  margin-bottom: 36px;
}
.courses-item__image {
  width: 560px;
}
.courses-item__link {
  font-size: 1.25rem;
  font-weight: 600;
  letter-spacing: 0.2px;
  display: flex;
  align-items: center;
}
.courses-item:nth-child(even) .courses-item__right {
  order: -1;
}

.stories__title {
  margin-bottom: var(--title-gap);
}
.stories__more {
  margin-top: var(--title-gap);
  display: flex;
  justify-content: center;
}

.stories-item {
  border-radius: var(--border-radius);
  margin-bottom: var(--gap);
  padding-bottom: 32px;
  width: 32%;
}
.stories-item--width {
  width: 32%;
}
.stories-item--gap {
  width: 2%;
}
.stories-item__person {
  display: grid;
  grid-auto-flow: column;
  justify-content: start;
  gap: 16px;
  margin-bottom: 16px;
  padding: 24px 24px 0;
}
.stories-item__person-image {
  width: 60px;
  height: 60px;
}
.stories-item__person-name {
  line-height: 1;
  font-size: 1.5625rem;
  font-weight: 600;
  padding-top: 8px;
  margin-bottom: 6px;
}
.stories-item__person-job {
  /* body-2 */
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25;
  color: rgb(130, 130, 130);
}
.stories-item__description {
  font-size: 1.125rem;
  padding: 0 24px;
}

.stories-item--video .stories-item__person {
  padding-top: 18px;
  margin-bottom: 0;
}

.video {
  aspect-ratio: 16/9;
  display: block;
  background-color: black;
  background-image: var(--bg);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  border-top-left-radius: var(--border-radius);
  border-top-right-radius: var(--border-radius);
  position: relative;
}

.video-play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 56px;
  height: 56px;
  padding: 16px;
  border-radius: 100px;
  background: rgba(0, 0, 0, 0.8);
  border: 0;
}
.video-play::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform-origin: top left;
  display: block;
  content: "";
  width: 17px;
  height: 17px;
  transition: all 0.2s;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: center center;
}

.video:hover .video-play::before {
  scale: 1.2;
}

.teachers .section__wrapper {
  border-top: 1px solid rgb(224, 224, 224);
  padding-top: var(--title-gap);
}
.teachers__title {
  margin-bottom: var(--title-gap);
}
.teachers__items {
  display: grid;
  grid-auto-flow: column;
  gap: var(--gap);
  overflow: hidden;
}
.teachers__controls {
  margin-top: 46px;
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
  justify-items: center;
  gap: 24px;
}

.teachers-item {
  width: 320px;
}
.teachers-item__header, .teachers-item__body {
  margin-bottom: 20px;
}
.teachers-item__header {
  display: grid;
  justify-content: start;
  align-items: center;
  gap: 12px;
  grid-auto-flow: column;
}
.teachers-item__footer {
  padding-bottom: 10px;
}
.teachers-item__name {
  line-height: 1;
  font-size: 1.5625rem;
  font-weight: 600;
  margin-bottom: 8px;
}
.teachers-item__experience {
  font-size: 1.125rem;
  color: rgb(27, 154, 170);
  margin-bottom: 8px;
}
.teachers-item__description {
  /* body-2 */
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25;
  color: rgb(79, 79, 79);
}

.image-gradient-border {
  background: linear-gradient(270deg, rgb(27, 154, 170) 0%, rgb(245, 241, 227) 100%);
  padding: 6px;
  border-radius: 50%;
  display: inline-block;
}
.image-gradient-border > img {
  border-radius: 50%;
  box-shadow: 0 0 0 3px #fff;
}

.faq .section__wrapper {
  border-top: 1px solid rgb(224, 224, 224);
  padding-top: var(--title-gap);
}
.faq__title {
  margin-bottom: var(--title-gap);
}
.faq__items {
  max-width: 1160px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.faq-item {
  border-bottom: 1px solid rgb(224, 224, 224);
  padding: 24px 0;
}
.faq-item__title {
  line-height: 1;
  font-size: 1.5625rem;
  font-weight: 600;
}
.faq-item__header {
  display: grid;
  justify-content: start;
  align-items: center;
  grid-auto-flow: column;
  gap: 16px;
}
.faq-item__header:hover {
  cursor: pointer;
}
.faq-item__header:hover .faq-item__arrow:not(.button-arrow--filled) {
  color: var(--text-link);
  border-color: var(--text-link);
}
.faq-item__header:hover .faq-item__arrow:has(.button-arrow--filled) {
  background-color: var(--text-link-hover);
  border-color: var(--text-link-hover);
}
.faq-item__body {
  /* body-1 */
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.25;
  display: none;
  padding: 24px 56px;
}
.faq-item.is-active .faq-item__body {
  display: block;
}

.footer {
  padding: 40px 0;
}
.footer__wrapper {
  display: flex;
  justify-content: space-between;
}
.footer__left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.footer__copyright, .footer__copyright-mobile {
  font-weight: 500;
}
.footer__copyright small, .footer__copyright-mobile small {
  display: block;
  margin-top: 4px;
  color: rgb(79, 79, 79);
  font-size: 0.8125rem;
  font-weight: 400;
}
.footer__copyright-mobile {
  display: none;
}
.footer__navs {
  display: flex;
  gap: 80px;
}
.footer__nav {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.footer__nav a {
  color: black;
  font-weight: 500;
  font-size: 1rem;
}
.footer__nav a:hover {
  opacity: 0.6;
}
.footer__socials {
  display: flex;
  gap: 8px;
}

.process__title {
  margin-bottom: var(--title-gap);
}
.process__items {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-flow: column;
  gap: var(--gap);
}

.process-item {
  --accent-color: rgb(27, 154, 170);
  border-radius: var(--border-radius);
}
.process-item .video {
  flex: 0 0 auto;
}
.process-item__body {
  padding: 32px 40px 40px;
}
.process-item__title {
  line-height: 1;
  font-size: 1.9375rem;
  font-weight: 700;
  position: relative;
  margin-bottom: 24px;
}
.process-item__bubble {
  line-height: 1;
  font-size: 1.5625rem;
  font-weight: 600;
  position: relative;
  padding: 16px 24px;
  background-color: #fff;
  color: var(--accent-color);
  border-radius: 100px;
  display: inline-block;
  margin-bottom: 24px;
}
.process-item__bubble::before {
  display: block;
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  background-color: #fff;
  border-radius: 50%;
  left: 24px;
  top: 0;
  transform: translateY(-50%);
}
.process-item__list {
  padding-left: 22px;
}
.process-item__list li {
  margin-bottom: 20px;
}
.process-item__list li:last-child {
  margin-bottom: 0;
}
.process-item__list li::marker {
  color: var(--accent-color);
}
.process-item:nth-child(1) .process-item__title::after {
  display: block;
  content: "";
  position: absolute;
  z-index: 1;
  right: -100px;
  top: 10px;
  width: 80px;
  height: 80px;
  background-image: url(../images/decor-arrow-2.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.process-item:nth-child(2) {
  --accent-color: rgb(213, 122, 102);
}
.process-item:nth-child(2) .process-item__title::after {
  display: block;
  content: "";
  position: absolute;
  z-index: 1;
  right: -100px;
  top: 10px;
  width: 80px;
  height: 80px;
  background-image: url(../images/decor-arrow-3.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.prices__picker {
  display: flex;
  justify-content: center;
  gap: 8px;
  margin-bottom: 40px;
}
.prices__items {
  display: flex;
  gap: var(--gap);
}

.prices-item {
  display: flex;
  flex-direction: column;
  flex: 1 0 0;
  background-color: #fff;
  border-radius: var(--border-radius);
  padding: 40px;
}
.prices-item__button {
  display: block;
  text-align: center;
  width: 100%;
}
.prices-item__title {
  line-height: 1;
  font-size: 1.9375rem;
  font-weight: 700;
  color: var(--text-link);
  margin-bottom: 16px;
}
.prices-item__title--highlighted {
  color: rgb(213, 122, 102);
}
.prices-item__body {
  flex-grow: 1;
}
.prices-item__price {
  margin-bottom: 32px;
}
.prices-item__price-old {
  font-size: 1.25rem;
}
.prices-item__price-number {
  line-height: 1;
  font-size: 1.9375rem;
  font-weight: 700;
}
.prices-item__price-muted {
  color: #828282;
}
.prices-item__price--columns {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.prices-item__benefits {
  margin-bottom: 34px;
  display: flex;
  flex-direction: column;
  gap: 18px;
}
.prices-item__benefit {
  /* body-2 */
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25;
  display: flex;
  gap: 8px;
}
.prices-item__benefit .icon-svg {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  color: var(--text-link);
}

.icon-info {
  display: inline-block;
  vertical-align: bottom;
  width: 30px;
  height: 30px;
  color: #828282;
}

.icon-quote {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  background: rgb(209, 235, 238);
  color: #1B9AAA;
  border-radius: 100px;
}
.icon-quote .icon-svg {
  width: 50%;
  height: 50%;
}

.quote .section__wrapper {
  max-width: 920px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: start;
  gap: var(--gap);
}
.quote .icon-quote {
  margin-bottom: 12px;
}
.quote__text {
  font-weight: 400;
  font-size: 1.5625rem;
  line-height: 1.25;
}

.start__items {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: var(--gap);
}

.start-item {
  background-color: #fff;
  border-radius: var(--border-radius);
  padding: 40px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.start-item .button--sm {
  display: none;
}
.start-item__body {
  flex-grow: 1;
}
.start-item:nth-child(1)::after, .start-item:nth-child(2)::after {
  display: block;
  content: "";
  position: absolute;
  z-index: 1;
  right: -60px;
  top: 50%;
  width: 80px;
  height: 80px;
  transform: translateY(-50%);
  background-image: url(../images/decor-arrow-2.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.start-item:nth-child(2)::after {
  background-image: url(../images/decor-arrow-3.png);
}
.start-item__image-container {
  position: relative;
  background-color: var(--bg-red);
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  margin-bottom: 24px;
  aspect-ratio: 360/200;
}
.start-item__step {
  line-height: 1;
  font-size: 1.9375rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  position: absolute;
  top: 16px;
  left: 16px;
  background-color: #fff;
  border-radius: 50%;
}
.start-item__description {
  font-weight: 400;
  font-size: 1.375rem;
  line-height: 1.25;
  margin-bottom: 16px;
}

.timetable__dates {
  margin-bottom: 24px;
}
.timetable__items {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 2px;
}

.timetable-item {
  position: relative;
  padding: 20px;
}
.timetable-item__wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
  height: 100%;
}
.timetable-item__footer, .timetable-item__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.timetable-item__header {
  font-size: 1rem;
  font-weight: 500;
  line-height: 1;
}
.timetable-item__header .icon-svg {
  width: 24px;
  height: 24px;
  display: inline-block;
  color: var(--text-link);
  margin-right: 10px;
}
.timetable-item__body {
  flex-grow: 1;
}
.timetable-item__footer {
  font-size: 0.875rem;
  line-height: 1;
}
.timetable-item__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.1;
}
.timetable-item__type {
  color: var(--text-link);
  display: flex;
  align-items: center;
}
.timetable-item__time {
  display: flex;
  align-items: center;
}
.timetable-item__teacher {
  font-weight: 500;
  display: flex;
  align-items: center;
}
.timetable-item__teacher-picture {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin-right: 12px;
}
.timetable-item__difficulty {
  color: var(--text-link);
  font-weight: 400;
}

.first-lesson {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.first-lesson-item {
  background-color: #fff;
  border-radius: var(--border-radius);
  padding: 16px;
  display: flex;
  align-items: center;
  gap: 16px;
}
.first-lesson-item__image {
  width: 144px;
  aspect-ratio: 144/80;
  border-radius: 16px;
}

.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(16px);
  z-index: 4;
  opacity: 0;
  transition: opacity 0.3s;
  justify-content: center;
  align-items: center;
}
.popup.is-shown {
  opacity: 1;
}
.popup-window {
  background-color: var(--bg-blue);
  overflow-y: auto;
  width: 520px;
  border-radius: var(--border-radius);
  margin-left: 16px;
  margin-right: 16px;
}
.popup-window__title {
  line-height: 1;
  font-size: 1.9375rem;
  font-weight: 700;
  padding-bottom: 32px;
}
.popup-window__content {
  padding: 20px 40px 40px;
}
.popup__close {
  padding: 16px 16px 0 0;
}
.popup__close button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: rgb(130, 130, 130);
  background-color: #fff;
  margin-left: auto;
}
.popup__close button:hover {
  color: black;
}
.popup__close .icon-svg {
  width: 24px;
  height: 24px;
}

.popup--nav {
  justify-content: flex-end;
  align-items: stretch !important;
}
.popup--nav__singup {
  display: none;
}
.popup--nav .popup-window {
  position: relative;
  width: 330px;
  margin: 0;
  border-radius: 0;
  border-bottom-left-radius: var(--border-radius);
}
.popup--nav .popup__close {
  align-items: flex-end;
}
.popup--nav .popup__nav {
  display: flex;
  flex-direction: column;
}
.popup--nav .popup__nav a {
  color: black;
  padding: 16px;
  font-size: 18px;
  font-weight: 500;
  border-bottom: 1px solid rgba(27, 154, 170, 0.2);
}

